body{
    padding: 70px;
}

p{
    cursor: pointer;
    margin: 0;
    white-space: nowrap;
    overflow: hidden;
}


.list-group-item{
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.complete{
    text-decoration: line-through;
}

.overflow-ellipsis {
    text-overflow: ellipsis;
  }

@media (max-width: 576px) { 
    body{
        padding: 20px;
    }
}